import React, { useState, useEffect } from "react";
import './components/utility_nfts.css';
//import axios from 'axios';
import { useTranslation } from 'react-i18next';

import Modal  from './components/modals/cofreclaimespecial.jsx';

//const link="http://localhost:4000";










function CofresE() {
  const [isOpen, setIsOpen] = useState(false);
  const [cofresEProcesando, setCofresEProcesando] = useState({});
 // const [cofresEDisponibles, setCofresEDisponibles] = useState(null);
  const [walletData, setWalletData] = useState(null);
  //const [statuscofresE, setStatuscofresE] = useState(null);
  const { t } = useTranslation();






  useEffect(() => {
    const user = localStorage.getItem("user");




    // Simulacin de retraso de medio segundo (500 milisegundos)
    const delay = 500;

    // Realizar la solicitud de Fetch aqu despus del retraso
    setTimeout(() => {
      fetch(`/wallet/?usuario=${user}`)
        .then((response) => response.json())
        .then((data) => {
          const balanceDrops = data.body[0].balance_cofre_e;
          const retirosDrops = 0;//data.body[0].retiros_drops;
          const result = (balanceDrops - retirosDrops).toFixed(2);
          setWalletData(result);
        })
        .catch((error) => console.log(error));
    }, delay);
  }, []);

  console.log("walletData", walletData);







function handleClaim(reclamo, cofresEAReclamar) {

   let username=localStorage.getItem("user");
  if (cofresEAReclamar >= 1) {
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ user_cofres: { user_claim: username, cofres: 1 }})
    };
    fetch(`/chest_claim_especial?user_cofres`, requestOptions)
      .then(response => response.json())
      .then(data => {console.log(data)
        //alert(data.body)
      if(data.error){
        alert(data.error);
      }else{
      setCofresEProcesando(data.body);
         setIsOpen(!isOpen);
      }
        })
      .catch(error => console.log(error));
  } else {
    alert(`You don't have what it takes to open ${reclamo} chests`);
  }
}


const onClose = () => {
        setIsOpen(!isOpen);
  };



return (   
<div className="wrapp_especial">

  <div class="cofres-disponibles">
  <h4>{t('cofreE.cofreE')}</h4>
    <img src="image/cofres/cofre_especial.png" alt='Chest' style={{ width: "7em", height: "7em" }} />
     



     <div class="cofres-disponibles-qty">
    <h2>{walletData}</h2>

    <button type="button" onClick={() => handleClaim(1, walletData)}>
      {t('cofreE.claim')}
    </button>

   
  </div>
  </div>
     <Modal isOpen={isOpen} onClose={onClose} cofresProcesando={cofresEProcesando} /> 
   
</div>
  );
}






export default CofresE