import "./infoballoModal.css";

const ModalNegoti= ({ children, isOpenNeg, closeModalNeg, propsi }) => {
  const handleModalContainerClickn = (e) => e.stopPropagation();

  return (
    <article className={`modal ${isOpenNeg && "is-open"}`} onClick={closeModalNeg}>
      <div className="modal-container" onClick={handleModalContainerClickn}>
        <button className="modal-close" onClick={closeModalNeg}>
          X
        </button>
         <div className="card-info">
       
          <div className="content"> 
           <div className="content-img"> 
         <img src={propsi.Imagen} alt={propsi.name} className="icono"  />
         </div>
        
           <div className="name">
          <p>{propsi.name}</p>
         </div>
         <div className="actions">
<input type="submit" name="background"  value="VENDER EJEMPLAR"/>


</div>
       <div className="actions">

<input type="submit" name="background"  value="MATRICULAR YOQUEY"/>


</div>
       <div className="actions">

<input type="submit" name="background"  value="ASOCIAR ESTABLO"/>


</div>

       <div className="actions">

<input type="submit" name="background"  value="HABILITAR CRIA"/>

</div>


       </div>
       </div>
        </div>
    </article>
  );
};

export default ModalNegoti;