import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './modalinFoRace.css';
import { useTranslation } from 'react-i18next';
import RaceSimulator from "./../../../races/simuladordecarrera.jsx";



// Dentro del componente:


const link = 'http://localhost:4000';





const ModalinFoRace = ({  isOpen, closeModalInf, raceId }) => {
  const [actuaciones, setActuaciones] = useState([]);
   const [equinosC, setEquinosC] = useState([]);
   const [showEquinos, setShowEquinos] = useState(false);
  const [error, setError] = useState('');
  const { t } = useTranslation();
 const [isHovered, setIsHovered] = useState(false);

  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
}




  
 console.log("RAAAAACEID",raceId,raceId.raceid)
  const handleModalContainerClicke = (e) => e.stopPropagation();


  useEffect(() => {
    const fetchActuaciones = async () => {
      try {
        console.log(raceId.raceid)
        const response = await fetch(`/actuaciones?race=${raceId.raceid}`);
        const registro = await fetch(`/race?raceid=${raceId.raceid}`);
        const data = await response.json();
        const registros = await registro.json();
        const registrados = await registros.body[0].registrados;


        console.log("DATEROSSSS",data)
        console.log("registros",registros)
        console.log("registrAdis",registros.body[0].registrados)



   if(registrados.length>1){

    const numerosConsulta = registrados.join(',');

axios.get(`/equino?equineId=${numerosConsulta}`)
  .then(response => {
    const equinos = response.data.body;
    setEquinosC(prevEquinosC => [...prevEquinosC, ...equinos]);
    setShowEquinos(true)
  })
  .catch(error => {
    console.error(error);
  });
  
  // registrados.forEach((numero) => {
  //   axios.get(`${link}/equino?equineId=${numero}`)
  //     .then(respons => {
  //       setEquinosC(prevEquinosC => [...prevEquinosC, respons.data.body[0]]);
  //     })
  //     .catch(error => {
  //       console.error(error);
  //     });//
 //  // });

 }

   


        if (data.error) {
          setError(data.error);
        } else {
          setActuaciones(data.body);
        }
      } catch (error) {
        setError('Error al cargar las actuaciones.');
      }
    };

    if (raceId) {
      fetchActuaciones();
    }
  }, [raceId]);


  console.log("equinosC",equinosC)

  return (
<article className={`modal ${isOpen && "is-open"}`} onClick={closeModalInf}>
  <div className="modal-container-race-results" onClick={handleModalContainerClicke}>
  {/*  <button className="modal-close" onClick={closeModalInf}>X</button>
    */}<div className="card-info">
     {/*  <RaceSimulator />*/}


      <div className="results-races">
        {error && <p className="error">{error}</p>}
        <p className="race-name">{raceId.name}</p>
        {actuaciones.length > 0 ? (
          <table>
            <thead>
              <tr>
                <th><h2 title={t('resultados.lugar')}></h2></th>
                <th><h2 title={t('resultados.mint')}></h2></th>
                <th><h2 title={t('resultados.total')}></h2></th>
            
            
                <th><h2 title={t('resultados.add')}>:</h2></th>
               
              
              </tr>
            </thead>
            <tbody>
              {actuaciones
                .sort((a, b) => b.Total - a.Total) // Ordenar de mayor a menor por Total
                .map((actuacion, index) => (
                  <tr key={actuacion._id}>
                  
                    <td> 

  <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
  {index + 1}
  <h1 style={{  color: "gold" }}>
  
</h1></div>
 <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
  {parseInt(actuacion.Total)}<h4 style={{  color: "gold" }}>&nbsp;Pts</h4> </div>
 <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
    
  </div>
    <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center',bottom:"0.5rem" }}>
  {actuacion.account }
  </div>

                     </td>
   
                    <td>   
                    <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
  {actuacion.sexo === 'M' ? '' : ''} <span style={{ fontSize: '0.7rem' }}>&nbsp;&nbsp;{actuacion.tipo}&nbsp;&nbsp;</span>
 {actuacion.equineId}
</div>
     <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
      <img
        src={`/image/equinos/${actuacion.equineId}.png`}
        alt="Img"
        className={`icono-races ${isHovered ? 'hovered' : ''}`}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
      />
    </div>
   <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
  <h5>{actuacion.name}</h5> </div>

 </td>


 <td>
   <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
  
  <span style={{ fontSize: '0.7rem' }} >{t('resultados.agilidad')}</span>&nbsp; {actuacion.agilidad}
  </div>

<div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
  <span style={{ fontSize: '0.7rem' }}>{t('resultados.resistencia')}</span>&nbsp;
  {actuacion.resistencia + parseInt(actuacion.resistencia_add)}
</div>
     
     <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
  <span style={{ fontSize: '0.7rem' }}>{t('resultados.velocidad')}</span>&nbsp;
  {actuacion.velocidad + parseInt(actuacion.velocidad_add)}
</div>

     <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
       <span style={{ fontSize: '0.7rem' }}>Ramdon:</span>/{actuacion.divisor}*{actuacion.multiplicador} 
  </div>
      <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
        T:&nbsp;<span style={{ fontSize: '1rem' }}>{parseInt(actuacion.Total_1)}</span> &nbsp; Pts
      </div>
</td>
 

                    <td>
                      
   <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
    <span style={{ fontSize: '0.7rem' }}> {t('resultados.salud')} </span> &nbsp;&nbsp;{parseInt(actuacion.Total_animo)}/<span>{parseInt(actuacion.animo)}</span>
  </div>
      <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
     <span style={{ fontSize: '0.7rem' }}>{t('resultados.implementos')}   </span>&nbsp;&nbsp;{parseInt(actuacion.Total_implemento)}/<span>{parseInt(actuacion.implemento)}</span>
  </div>

 <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' , padding: '1rem' }}>
  T:&nbsp;<span style={{ fontSize: '1rem' }}>{parseInt(actuacion.Total_animo)  + parseInt(actuacion.Total_implemento)}</span>Pts
</div>


                    </td>
                 
                
                    
                    
                  </tr>
                ))}
            </tbody>
          </table>
        ) : (
        <div className="races_info">
        <div><h1>{t('resultados.nohaydato')}</h1></div>
        <div>

{ showEquinos && equinosC ? (
          <table>
            <thead>
              <tr>
                <th>{t('resultados.propietario')}</th>
                <th></th>
                 <th>{t('resultados.estadisticas')}</th>
             </tr>
            </thead>
            <tbody>
              { equinosC
                .map((eq, index) => (
                  <tr key={eq._id}>
                    <td>
  <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
    
  </div>
   <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
{eq.account} 
  </div>

  


  
</td>
  <td>
   <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
  {eq.sexo === 'M' ? '' : ''} <span style={{ fontSize: '0.7rem' }}>&nbsp;&nbsp;{eq.tipo}&nbsp;&nbsp;</span>
 {eq.equineId}

  </div>
     <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
      <img
        src={`/image/equinos/${eq.equineId}.png`}
        alt="Img"
        className={`icono-races ${isHovered ? 'hovered' : ''}`}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
      />
    </div>
   <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
  <h5>{eq.name}</h5> </div>

 
</td>

<td>
   <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
  
  <span style={{ fontSize: '0.7rem' }} >{t('resultados.agilidad')}</span>&nbsp; {eq.agilidad}
  </div>
     <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
 <span style={{ fontSize: '0.7rem' }}>{t('resultados.resistencia')}</span>&nbsp; {eq.resistencia}+<span>{isNaN(parseInt(eq.resistencia_add)) ? 0 : parseInt(eq.resistencia_add)}</span>
  </div>
     <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
<span style={{ fontSize: '0.7rem' }}>{t('resultados.velocidad')}</span>&nbsp; {eq.velocidad}+<span>{isNaN(parseInt(eq.velocidad_add)) ? 0 : parseInt(eq.velocidad_add)}</span>
  </div>
     <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
     <span style={{ fontSize: '0.7rem' }}> {t('resultados.salud')}</span>&nbsp;&nbsp;{parseInt(eq.animo)}
  </div>
      <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
      <span style={{ fontSize: '0.7rem' }}> {t('resultados.implementos')}</span>&nbsp;&nbsp;{isNaN(parseInt(eq.implemento)) ? 0 : parseInt(eq.implemento)}
  </div>
</td>
  
                  </tr>
                ))}
            </tbody>
          </table>):null }

        </div>
        </div>
        )}
      </div>
    </div>
  </div>
</article>
  );
};


export default ModalinFoRace;

