        merchant_image="https://www.blockhorseracing.com/image/logo/logoBlock.png"
        memo="Purchase Made By HIVEBUYER"
        base_currency="USD"
        pay_currency="BUDS,SOULS,HUESO,SWAP.HIVE,SWAP.HBD,HIVE,HBD"
