import React, { useState } from 'react';
import axios from 'axios';
import Icon from './../../../generals/svgiconos/svgiconosenreact.jsx';
import './components/Wallets/Wallet.css';
import FetchWallet from "./components/Wallets/components/fetchWallet.jsx"

const Wallet = () => {
  const [showTable, setShowTable] = useState(false);
  const [walletData, setWalletData] = useState(null); // Nuevo estado para almacenar los datos de walletData

  const handleDivClick = () => {
    setShowTable(!showTable);
  };



  return (
    <div className="wallet" onClick={handleDivClick} style={{ cursor: 'pointer' }}>
      <Icon name="wallet" width="1.5rem" height="1.5rem" className="svg" />
      {showTable && (
        <table>
          <tbody>
            <tr>
              <td>  
                {/* Pasar walletData y handleFetch como props al componente FetchWallet */}
                <FetchWallet />
                {/* Aqu puedes ajustar el valor 100 por el valor necesario para tu lgica de envo */}
              </td>
            </tr>
          </tbody>
        </table>
      )}
    </div>
  );
};

export default Wallet;

