import React, { useState } from 'react';
import NavbarNft from "./components/navbar_nft/nav_bar_nft.jsx"
import Caballos from"./../../../fetch/Caballos.jsx"
import Cofres from"./../../../fetch_nft/cofres.jsx"
import Cofresespeciales from"./../../../fetch_nft/cofresespeciales.jsx"
import Cofresmagicos from"./../../../fetch_nft/cofresmagicos.jsx"
import NftU from"./../../../fetch_nft/utility_nfts.jsx"
import NftE from"./../../../fetch_nft/implementos_nfts.jsx"
import Wallet from'./Wallet.jsx';
import CountdownClock from './components/temporada/temporada.jsx' 

function Dashboard() {
  const [currentRoute, setCurrentRoute] = useState('/caballos');

  const handleNavClick = (route) => {
    setCurrentRoute(route);
  }

  let componentToRender;
  switch(currentRoute) {
    case '/caballos':
      componentToRender = <><CountdownClock/>, <Caballos /></>;
      break;
    case '/nft_u':
      componentToRender =<><Cofresespeciales/><Cofres/>, <NftU /></>;
      break;
    case '/nft_e':
      componentToRender =<><Cofresmagicos/>, <NftE /></>;
      break;
    default:
      componentToRender = <Caballos />;
      break;
  }

  return (
    <div>
     <Wallet/>
      <NavbarNft handleNavClick={handleNavClick}/>
      {componentToRender}
    </div>
  )
}

export default Dashboard;

