import React from 'react';

import { useContext } from 'react';
import{NavLink} from'react-router-dom'

import './nav_bar_nft.css';
import LanguageContext from '../../../../../context/LanguageContext';
import ThemeContext from '../../../../../context/ThemeContext';
import Icon from './../../../../../generals/svgiconos/svgiconosenreact.jsx';

//function Navbar_nft(props) {
  

export default function Navbar_nft(props) {
  const { theme, handleTheme } = useContext(ThemeContext);
     const { handleNavClick } = props;



  const handleClick = (event, route) => {
    event.preventDefault();
    handleNavClick(route);
  }

  return (
    <nav className={theme}>
      <div className={'link-initial' + theme}>
        <ul className={props.theme}>
         <li>   
         <div class="Celda">  
       <a href="/caballos" onClick={(event) => handleClick(event, '/caballos')}>
            
    <Icon name="nav_bar_caballo" width="2rem" height="2rem" className="svg"/>
                                    
  </a>
 </div>


  <div class="Celda">  
                               

 <a href="/nft_u" onClick={(event) => handleClick(event, '/nft_u')}>       

<Icon name="nav_bar_manzana" width="2rem" height="2rem" className="svg"/>

  </a>
 </div>
  <div class="Celda">
         <a href="/nft_e" onClick={(event) => handleClick(event, '/nft_e')}>

<Icon name="nav_bar_trailer" width="2rem" height="2rem" className="svg"/>
  </a>
 </div>

           </li>
        </ul>
      </div>
    </nav>
  );
}




