import React, { useState } from 'react';
import Market_bar from "./components/navbar_market/market_bar.jsx"
import Market_Ballos from"./components/marketBallo.jsx"
import Nft_food from"./components/food.jsx"
import Nft_implementos from"./components/implementos.jsx"

function Dashboard() {
  const [currentRoute, setCurrentRoute] = useState('/market');

  const handleNavClick = (route) => {
    setCurrentRoute(route);
  }

  let componentToRender;
  switch(currentRoute) {
    case '/marketballos':
     componentToRender = <Market_Ballos />;
      break;
    case '/food':
      componentToRender = <Nft_food />;
      break;
    case '/implementos':
      componentToRender = <Nft_implementos />;
      break;
    default:
      componentToRender = <Nft_food/>;
      break;
  }

  return (
    <div>
      <Market_bar handleNavClick={handleNavClick}/>
      {componentToRender}
    </div>
  )
}

export default Dashboard;

