import { useContext } from "react";

import { useTranslation } from 'react-i18next';
import ThemeContext from "../context/ThemeContext";
import "./fotter.css"
import hkLogo from"./../../assets/hk-logo.png"
import boneLogo from"./../../assets/bigbone-logo.png"
import inferLogo from"./../../assets/infernal-logo.png"

const FooterContext = () => {
  const { theme } = useContext(ThemeContext);
   const { t } = useTranslation();


  return (
    <footer className={theme}>
    <div className="container-footer">
      <h2>  {t('footer.asociados')} </h2>
    <a href="https://infernalcoliseum.com/" target="_blank"><img className="logo" src={inferLogo} alt="Infernals" /></a>
<a href="https://www.hashkings.app/" target="_blank"><img className="logo"  src={hkLogo} alt="Kings" /></a>
<a href="https://www.mundovirtualhive.online/" target="_blank"><img className="logo" src={boneLogo} alt="BigDogBone" /></a>

    </div> </footer>
  );
};

export default FooterContext;
