import React, { useState, useEffect } from 'react';
import './racesin.css';
import ModalinFoRace from './components/modal/ModalinFoRace';
import { useTranslation } from 'react-i18next';

const link = 'http://localhost:4000';
  let initialValue=false
const RacesComponent = () => {
 const [isOpenInf, setIsOpenInf] = useState(initialValue);
  const [races, setRaces] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [selectedRace, setSelectedRace] = useState({ raceid: '', name: '' });
  const { t } = useTranslation();

  // const openModal = (raceid, name) => {
    
  // };

   const openModalInf = (raceid, name) => { 
    setIsOpenInf(true);
    setSelectedRace({ raceid, name });
    console.log("EEESSSSTA ABRIDO",isOpenInf)
  }

   const closeModalInf = () => setIsOpenInf(false);

  const handlePreviousPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  const handleNextPage = () => {
    setCurrentPage(currentPage + 1);
  };

 useEffect(() => {
  const fetchRaces = async () => {
    try {
      const response = await fetch(`/race?page=${currentPage}`);
      const data = await response.json();

      // Sort races in descending order based on raceid
      const sortedRaces = data.body.sort((a, b) => b.raceid - a.raceid);

      setRaces(sortedRaces);
    } catch (error) {
      console.error('Error al obtener las carreras:', error);
    }
  };

  fetchRaces();
}, [currentPage]);

  return (
    <div className="carreras-container">
      <h1>{t('racein.carrera')}</h1>
      <button onClick={handlePreviousPage} disabled={currentPage === 1}>
        {t('racein.anterior')}
    
      </button>
      <button onClick={handleNextPage}>{t('racein.siguiente')}</button>

      <ul>
        {Array.isArray(races) && races.length > 0 ? (
          races.map((race) => (
            <li key={race._id} onClick={() => openModalInf(race.raceid, race.name)}>
              <span className="raceid">{race.raceid}</span>
              <span className="name">{race.name}</span>
             <span className="status">
  {race.status_superior === 'abierta' ? 'Open' : race.status_superior === 'en_proceso' ? 'In Process' : race.status_superior}
</span>
            </li>
          ))
        ) : (
          <li>{t('resultados.nohaydato')}</li>
        )}
      </ul>
  

      {isOpenInf && (  <ModalinFoRace isOpen={isOpenInf} closeModalInf={closeModalInf}   raceId={selectedRace}     >

         


    </ModalinFoRace> )}


    </div>
  );
};

export default RacesComponent;
     